@import 'reset';
@import 'mixins';
@import 'variables';

body {
    @include font(18px, 21px, 'Raleway', 400);
}
.banner {
    background-image: url(images/img.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-bottom: 116px;
    padding: 230px 0 120px;
    position: relative;

    @media only screen and (max-width: 1024px) {
        & {
            padding-bottom: 126px;
            margin-bottom: 78px;
        } 
    }
    @media only screen and (max-width: 767px) {
        & {
            padding: 106px 0 52px;
            margin-bottom: 40px;
        }
    }

    &__container {
        @include container();

        @media only screen and (max-width: 767px) {
            &.hide {
                opacity: 0;
            }
        }
    }
    &::before {
        content: '';
        display: block;
        background-color: #252B49;
        opacity: .8;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }
    &__title {
        color: $color;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 31px;
        position: relative;
        z-index: 1;

        @include font(64px, 75px,'Raleway', 900);
        @media only screen and (max-width: 1024px) {
            & {
                @include font(48px, 56px,'Raleway', 900);
                margin-bottom: 62px;
            }
        }
        @media only screen and (max-width: 767px) {
            & {
                @include font(36px, 42px,'Raleway', 900);
                margin-bottom: 33px;
            }
        }
    }
    &__text {
        color: $color;
        text-align: center;
        position: relative;
        z-index: 1;
        max-width: 1000px;
        margin: 0 auto;
        margin-bottom: 80px;
        
        @include font(24px, 28px,'Raleway', 400);

        @media only screen and (max-width: 1024px) {
            & {
                @include font(18px, 21px,'Raleway', 400);
                margin-bottom: 89px;
            }
        }
        @media only screen and (max-width: 767px) {
            & {
                @include font(14px, 16px,'Raleway', 400);
                margin-bottom: 50px;
            }
        }
    } 
}
.header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;

    @media only screen and (max-width: 1024px) {
        & {
            margin-bottom: 157px;
            }
        }
    @media only screen and (max-width: 767px) {
        & {
            padding: 22px 0;
            align-items: center;
            border-bottom: none;
            margin-bottom: 0;
        }
    }

    &__container {
        @include container();
        border-bottom: 1px solid $color_border;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media only screen and (max-width: 767px) {
            & {
                border-bottom: none;
            }

        }
    }
    &__burger {
        // & svg {
        //     display: none;
        //     position: relative;
        //     z-index: 1;
        //     @media only screen and (max-width: 767px) {
        //         & {
        //             display: block;
                    
        //         } 
        //     }
        // }
        display: none;
        z-index: 2;
        width: 24px;
        height: 18px;
        position: relative;
        @media only screen and (max-width: 767px) {
            & {
                display: block;
        }
        &::before {
            content: '';
            display: block;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            border-bottom: 3px solid $color;
            transition: all .3s linear;
        }
        &::after {
            
            content: '';
            display: block;
            width: 100%;
            position: absolute;
            left: 0;
            bottom: 0;
            
            border-bottom: 3px solid $color;
            transition: all .3s linear;
        }
    }
        &.open {
            .header__burger-item {
                opacity: 0;
                transition: all .3s linear;
            }
            &::after {
                transform: rotate(-45deg) translate(5px, -5px);
            }
            &::before {
                transform: rotate(45deg) translate(5.7px, 5.7px);
            }
        }
    }
    &.scrolled {
        background-color: #2f3047;
    }
    &__burger-item {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        border-bottom: 3px solid $color;
        width: 100%;
    }
}
.nav {

    &__list {
        display: flex;
        align-items: center;
    }
    
    &__link {
        color: $color;
        text-align: center;
        text-transform: uppercase;
        padding: 35px 5px;
        display: inline-block;
        min-width: 134px;
        padding-bottom: 36px;
        border-bottom: 3px solid transparent;
        @include font(13px, 15px,'Raleway', 700);
        transition: all .3s linear;

        @media only screen and (max-width: 1024px) {
            & {
                padding-left: 30px;
                padding-right: 30px;
                @include font(12px, 14px,'Raleway', 700);
            }
        }
        @media only screen and (max-width: 992px) {
            & {
                padding-left: 15px;
                padding-right: 15px;
                min-width: 124px;
            }
        }
        &:hover {
            border-bottom: 3px solid $color_border;
        }
        &:active {
            border-bottom: 3px solid $color_nav;
            color: $color_nav;
        }
    }
    @media only screen and (max-width: 767px) {
        & {
            display: none;
        }
        &.open {
            display: block;
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            height: 100vh;
            padding: 84px 20px 20px;

            .nav__list {
                display: block;
                
                .nav__link {
                    display: block;
                    text-align: left;
                    padding: 24px 0;
                }
            }
        }
    }
}

.btn {
    color: $color;
    text-align: center;
    text-transform: uppercase;
    padding: 26px 70px;
    background-color: $background_btn;
    border: none;
    position: relative;
    z-index: 1;
    display: block;
    margin: 0 auto;
    cursor: pointer;
    transition: all .3s linear;

    @include font(24px, 28px,'Raleway', 900);
    @media only screen and (max-width: 1024px) {
        & {
            padding: 26px 55px;
        }
    }
    @media only screen and (max-width: 767px) {
        & {
            padding: 16px 38px;
            width: 100%;
        }
    }

    &:hover {
        background-color: $color_nav;
    }
    &:active {
        background: $background_btn;
        box-shadow: 4px 4px 30px 0px $background_btn;
    }
}

.link__logo {
    display: flex;
    align-items: center;
    z-index: 1;

    @media only screen and (max-width: 1024px) {
        & {
            svg {
                width: 112px;
                height: 28px;
            }
        }
    }
    @media only screen and (max-width: 767px) {
        &  {
            svg {
                width: 103px;
                height: 14px;
            }
        }  
    }
}
// features
.features {
    &__container {
        @include container();
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
        row-gap: 88px;
        margin-bottom: 133px;
        @media only screen and (max-width: 1024px) {
            column-gap: 20px;
            row-gap: 50px;
            margin-bottom: 80px;
        }
        @media only screen and (max-width: 767px) {
            grid-template-columns: 1fr;
            row-gap: 42px;
        }
    }
    &__wrapper {
        text-align: center;  
    }
    &__wrapper-svg {
        display: flex;
        width: 85px;
        height: 85px;
        align-items: center;
        justify-content: center;
        margin: 0 auto 29px;

        & svg {
            width: 100%;
            height: 100%;
        }
        @media only screen and (max-width: 1024px) {
            margin-bottom: 25px;
            svg {
                width: 85px;
                height: 85px;
            }
        }
        @media only screen and (max-width: 767px) {
            margin-bottom: 21px;
            svg {
                width: 75px;
                height: 75px;
            }
        }
        
    }
    &__title {
        color: $color_title;
        @include font(24px, 28px,'Raleway', 900);
        text-transform: uppercase;
        margin-bottom: 42px;
        @media only screen and (max-width: 1024px) {
            margin-bottom: 23px;
            @include font(18px, 21px,'Raleway', 900);
        }
        @media only screen and (max-width: 767px) {
            margin-bottom: 15px;
        }
    }
    &__text {
        color: $color__text;
        @include font(18px, 21px,'Raleway', 400);
        @media only screen and (max-width: 1024px) {
            @include font(14px, 16px,'Raleway', 400);
        }
    }
}
// works
.works {
    margin-bottom: 114px;
    @media only screen and (max-width: 1024px) {
        margin-bottom: 78px;
    }
    @media only screen and (max-width: 767px) {
        margin-bottom: 35px;
    }
    &__container {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 2px;
        @media only screen and (max-width: 767px) {
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__wrapper-image {
        position: relative;
        padding-top: 100%;
    }
    &__image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
    &__wrapper-content {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $background_img_hover;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        opacity: 0;
        transition: all .3s linear;
        cursor: pointer;
        &:hover {
            opacity: 1;
        }
        &:active {
            background-color: $background_img_active;
        }
    }
    &__title {
        color: $color;
        @include font(24px, 28px,'Raleway', 900);
        text-transform: uppercase;
        margin-bottom: 20px;
        @media only screen and (max-width: 1024px) {
            margin-bottom: 13px;
            @include font(18px, 21px,'Raleway', 900);
        }
        @media only screen and (max-width: 767px) {
            margin-bottom: 5px;
        }
    }
    &__text {
        color: $color;
        @include font(18px, 21px,'Raleway', 400);
        @media only screen and (max-width: 1024px) {
            @include font(14px, 16px,'Raleway', 400);
        }
    }
}
.our-team {
    margin-bottom: 114px;
    @media only screen and (max-width: 1024px) {
        margin-bottom: 78px;
    }
    @media only screen and (max-width: 767px) {
        margin-bottom: 34px;
    }
    &__container {
        @include container();
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 30px;
        @media only screen and (max-width: 767px) {
            grid-template-columns: 1fr;
            row-gap: 50px;
        }
    }
    &__wrapper-image {
        margin-bottom: 30px;
        position: relative;
        padding-top: 100%;
        @media only screen and (max-width: 1024px) {
            margin-bottom: 25px;
        }
    }
    &__image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
    &__title {
        color: $color_title;
        @include font(24px, 28px,'Raleway', 700);
        text-transform: capitalize;
        margin-bottom: 5px;
        @media only screen and (max-width: 1024px) {
            @include font(18px, 21px,'Raleway', 700);
        }
    }
    &__subtitle {
        color: $color__subtitle;
        @include font(18px, 21px,'Raleway', 400);
        text-transform: uppercase;
        margin-bottom: 18px;
        @media only screen and (max-width: 1024px) {
            @include font(14px, 16px,'Raleway', 400);
            margin-bottom: 22px;
        }
        @media only screen and (max-width: 767px) {
            margin-bottom: 38px;
        }
    }
    &__text {
        color: $color__text;
        @include font(18px, 21px,'Raleway', 400);
        margin-bottom: 38px;
        @media only screen and (max-width: 1024px) {
            @include font(14px, 16px,'Raleway', 400);
            margin-bottom: 39px;
        }
        @media only screen and (max-width: 767px) {
            margin-bottom: 55px;
        }
    }
}
.wrapper__social-media {
    display: flex;
    column-gap: 6px;
}
    .facebook__link, 
    .twitter__link,
    .linkedin__link {
        border: 1px solid $color_border-icon;
        width: 39px;
        height: 39px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: fill .3s linear;
        &:hover {
            border: 1px solid $color_nav;
        }
        &:active {
            border: 1px solid $background_img_active;
        }
        @media only screen and (max-width: 1024px) {
            width: 32px;
            height: 32px;
        }
        @media only screen and (max-width: 767px) {
            width: 39px;
            height: 39px;
        }

    }

.facebook,
.linkedin,
.twitter {
    transition: fill .3s linear; 
    &:hover path {
        fill: $color_nav;
    }
    &:active path {
        fill: $background_img_active;
    }
}